.app {
  /* text-align: center; */
  padding: 20px;
  font-family: sans-serif;
}

image-container {
  position: relative;
  display: inline-block;
  max-width: 100%;
}

.uploaded-image {
  width: 100%;
  max-width: 600px;
  height: auto;
  border: 1px solid #ccc;
}

.overlay-text {
  position: absolute;
  color: red;
  font-weight: bold;
  font-size: 24px;
  cursor: move;
}

.text-input {
  margin-top: 10px;
  padding: 8px;
  font-size: 16px;
  width: 100%;
  max-width: 600px;
}